import React, { useEffect } from "react";
import { Icon } from "@iconify/react";
import { useDispatch, useSelector } from "react-redux";
import { CardTitle, Table } from "reactstrap";
import { fetchTeamtimeoff, fetchTeamAttendance } from "../action/action";
import { Link } from "react-router-dom";

const TeamAttendance = () => {
  const dispatch = useDispatch();
  const timeOffData = useSelector((state) => state.login.teamattendance);
  const check = useSelector((state) => state);
  console.log(check);
  console.log(timeOffData);

  useEffect(() => {
    dispatch(fetchTeamtimeoff());
    dispatch(fetchTeamAttendance())
  }, [dispatch]);
  return (
    <>
       <div className="header">
        <div className="left-corner">
          <i className="user-icon bi bi-clock"></i>
          <div>Attendance</div>
        </div>
        <div className="tabs">
          <div className="tabs-bar">
            <div className="tabs-nav-container">
              <span className="tabs-tab-prev">
                <span className="tabs-tab-prev-icon">
                  <i className="tabs-tab-prev-icon-target"></i>
                </span>
              </span>
              <span className="tabs-tab-next">
                <span className="tabs-tab-next-icon">
                  <i className="tabs-tab-next-icon-target"></i>
                </span>
              </span>
              <div className="tabs-nav-wrap">
                <div className="tabs-nav-scroll">
                  <div className="tabs-nav">
                    <div className="tabs-tab">
                      <div className="hidden-div">
                        <Link
                        to="/attendance"
                          style={{
                            lineHeight: "55px",
                            display: "inline-block",
                          }}
                        >
                          MY ATTENDANCE
                        </Link>
                      </div>
                    </div>
                    <div className="tabs-tab">
                      <div className="hidden-div">
                        <Link   className="anchor"
                          
                          style={{
                            lineHeight: "55px",
                            display: "inline-block",
                          }}
                        >
                          TEAM ATTENDANCE
                        </Link>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="mytimeoff" style={{ marginTop: "120px" }}></div>

      <div className="myattedance">
        <Table className="no-wrap mt-3 align-middle " responsive borderless>
          <thead>
            <tr>
              <th>Employee Name</th>
              <th>Emp. Type</th>
              <th>Paid Tiem/Work Schedule</th>
              <th>Overtime</th>
              <th>Status</th>
              {/* <th>Status</th> */}
            </tr>
          </thead>
          <tbody>
            {timeOffData &&
              timeOffData.map((tdata, index) => (
                <tr key={index} className="border-top">
                  <td className="mb-0">{tdata.employeeName}</td>

                  <td>{tdata.fromDate}</td>
                  <td>{tdata.paidTime}</td>
                  <td>{tdata.overtime}</td>
                  <td className="scrollable-td">{tdata.status}</td>

                  {/* <td>
                    {tdata.statusName === "pending" ? (
                      <span className="p-2 bg-danger rounded-circle d-inline-block ms-3"></span>
                    ) : tdata.status === "holt" ? (
                      <span className="p-2 bg-warning rounded-circle d-inline-block ms-3"></span>
                    ) : (
                      <span className="p-2 bg-success rounded-circle d-inline-block ms-3"></span>
                    )}
                  </td> */}
                </tr>
              ))}
          </tbody>
        </Table>
      </div>
    </>
  );
};

export default TeamAttendance;
